#wilder_product_page{
  height: 100vh;
  width: 100vw;
  margin: 0px;
  display: flex;

  p{
    font-family: 'AvenirBold';
  }    

  #div1{
    flex: 4.5;
    &.hidden{

    }
  }
  
  #div2{
    flex: 4;
    position: relative;

    #black_product_image{
      background: url(../assets/black_product.jpeg);
      background-position: center;
      background-size: cover;
      position: absolute;
      height: 100vh;
      width: 100%;
      left: 0;
      transition-timing-function: cubic-bezier(.2,.87,0,.99);
      transition-duration: 2000ms;
    }

    #green_product_image{
      background: url(../assets/green_product.jpeg);
      background-position: center;
      background-size: cover;
      position: absolute;
      height: 100vh;
    }

    &.hidden{
      #black_product_image{
        width: 0px;
      }
    }
  }
  
  #div3{
    flex: 2.5;
    display: flex;
    flex-direction: column;

    #options_upper{
      flex: 3;
      background: #f6f6f6;
      padding: 80px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      #size_container{
        display: flex;
        flex-direction: column;
        justify-content: center;
      
        #sizes_upper{
          display: flex;
        }
        
        #sizes_lower{
          display: flex; 
        }    
      
        .size_item{
          width: 38px;
          height: 38px;
          border: 3px solid #333;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          margin: 6px;
        }
        
        .size_item:first-child{
          margin-left: 0px;
        }
        
        .size_item:last-child{
          margin-right: 0px;
        }
      }

      #details_container{
        
        #pricing{
          font-size: 2rem;
          color: #333;
          margin-bottom: 12px;
        }
        
        #add_to_cart{
          background: #666;
          padding: 8px 18px;
          font-family: 'AvenirRegular';
          font-size: 18px;
          color: #fff;
          border: none;
          height: 40px;
          margin-right: 12px;
          cursor: pointer;
        }
        
        #favorite{
          background: #666;
          border: none;
          color: #fff;
          height: 40px;
          width: 40px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
        }
        
        #details_actions{
          display: flex;
        }
      }
    }

    #options_lower{
      flex: 3.5;
      padding: 28px 48px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      #description{
        font-size: 20px;
        color: #333;
      }

      #product_image{
        background: #666;
        height: 150px;
      }

      #color_picker{
        display: flex;
        justify-content: center;

        #black_color_picker{
          height: 42px;
          width: 42px;
          background: #0d131a;
          border-radius: 24px;
          margin: 6px;

          &.active{
            height: 36px;
            width: 36px;
            border: 3px solid #bbb;
          }
        }

        #green_color_picker{
          height: 42px;
          width: 42px;
          background: #3e5742;
          border-radius: 24px;
          margin: 6px;

          &.active{
            height: 36px;
            width: 36px;
            border: 3px solid #bbb;
          }
        }
      }
    }
  }
  
}